<template>
  <section class="hotel-content">
    <div class="hot-logo">
      <h3><span class="red">HOT</span><span>EL</span></h3>
      <span class="title"></span>
    </div>
    <ul class="hotel-list">
      <li v-for="item in items">
        <router-link class="hot_img_box" :to="path + item.id">
          <app-img :src="item.images[0].imgurl" size="small"/>
          <span class="img_info">{{item.name}}</span>
        </router-link>
      </li>
    </ul>
  </section>
</template>

<script>
import AppImg from '../img/AppImg'
import Path from '../../js/path.js'
import Json from '../../js/json_data.js'
export default {
  name: 'hotels',
  data () {
    return {
      items: [],
      path: Path.hotelPAGE
    }
  },
  methods: {
    getdata () {
      let vm = this
      vm.$http.get(Path.dataURL + 'hotel.json').then(function (res) {
        // tjhzsjson
        let data = Json(res.body)
        data.forEach(function (_data) {
          let images = _data.images
          images.forEach(function (image) {
            let _url = image.url
            image.imgurl = _url
          })
        })
        vm.items = data
      }, function (err) {
        console.error(err + '\n' + '')
      })
    }
  },
  mounted: function () {
    this.getdata()
  },
  components: {AppImg}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!-- app-imgcontent.cssstyle -->
<!-- scoped -->
<style lang="scss">
@import '../../sass/content.scss';
.hotel-content{
  @extend %mainwidth;
  position: relative;
  background: #eee;
  height: 150px;
  margin: 40px auto;
  background-color: $bgcolor;
  box-shadow:  $shadow;
}
</style>



