<template>
  <div class="display-img" :style="_style_">
    <ul v-if='isLink' id="show_img" class="clearfix img_box" key="link">
      <li v-for="item in images" :style='size'>
        <router-link :to="item.url">
          <!-- appimgstyle,stylecss -->
          <app-img :src="item.imgurl" :css='size'/>
          <span class="img_desc">{{item.desc}}</span>
        </router-link>
      </li>
    </ul>
    <ul v-else id="show_img" class="clearfix img_box" key="img">
      <li v-for="item in images" :style='size'>
        <app-img :src="item.imgurl" :css="size"/>
        <span class="img_desc">{{item.desc}}</span>
      </li>
    </ul>
    <span class="control" id="control_left">&lt;</span>
    <span class="control" id="control_right">&gt;</span>
  </div>
</template>

<script>
import play from '../../js/PlayImg.js'
import AppImg from '../img/AppImg'
export default {
  name: 'main-content',
  props: {
    // 
    isLink: {
      type: Boolean,
      default: true
    },
    // 
    size: {
      type: Object,
      default () {
        return {
          height: '300px',
          width: '740px'
        }
      }
    },
    // CSS
    style: {
      type: Object
    },
    // 
    images: {
      type: Array
    }
  },
  computed: {
    _style_: function () {
      // 
      // ES6
      return Object.assign(this.size, this.style)
    }
  },
  methods: {
    play () {
      let _step = parseInt(this.size.width)
      // 
      play({
        step: _step, // ()
        time: 5000, // 
        fatherbox: '.display-img', // 
        imgbox: '.img_box',
        prev: '#control_left',
        next: '#control_right'
      })
    }
  },
  mounted: function () {
    this.play()
    // play
    this.$watch('images', function (now, old) {
      if (now !== old) {
        // console.log('************************')
        // console.log(now)
        this.play()
      }
    }, {deep: true})
  },
  components: {AppImg}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../sass/content.scss';
  .display-img{
    position: relative;
    margin: 0 auto;
    // height: 300px;
    // width: 740px;
    // float: left;
    background-color: #009688;
    box-shadow: $shadow;
  }
</style>



