import Vue from 'vue'
// 
import VueRouter from 'vue-router'
import routes from './routes.js'
// ajax
import VueResource from 'vue-resource'
// 
import Main from './layouts/Main'
// 
Vue.use(VueRouter)
Vue.use(VueResource)

const router = new VueRouter({
  base: __dirname,
  mode: 'history',
  routes: routes
})

new Vue({
  router: router,
  // 
  // 
  render: (h) => h(Main)
}).$mount('#app')




