<template>
  <footer class="footer">
    <div class="footer-box">
      <!--  v-bindprops,js -->
      <block v-for="item in items" :info="item"></block>
      <web-info :webinfo='webInfo'/>
    </div>
    <p class="version">&copy;&nbsp;&nbsp;{{version}}</p>
  </footer>
</template>

<script>
import Block from './footer/Block'
import WebInfo from './footer/Address'
import Path from '../js/path.js'
// 
import ResetData from '../data/footer.js'
import Json from '../js/json_data.js'
export default {
  name: 'footer',
  data () {
    return {
      items: ResetData.blockinfo,
      webInfo: ResetData.webinfo,
      version: ResetData.blockinfo.version
    }
  },
  mounted: function () {
    this.getinfo()
  },
  methods: {
    // 
    getinfo () {
      let _items = []
      let vm = this
      vm.$http.get(Path.dataURL + 'footer.json').then(function (res) {
        // console.log('ok')
        // tjhzsJSON.parse()
        let data = Json(res.body)
        // console.log('footer' + '\n' + data)
        // 
        for (let item of data.blockinfo) {
          if (item.link) {
            _items.push({
              title: item.title,
              smalltitle: item.smalltitle,
              // 
              url: item.url,
              // 
              pointer: item.link
            })
          } else {
            _items.push({
              title: item.title,
              smalltitle: item.smalltitle,
              pointer: item.link
            })
          }
        }
        // console.log(_items)
        vm.items = _items
        vm.webInfo = data.webinfo
        vm.version = data.webinfo.version
      }, function () {
        console.error('')
      })
    }
  },
  components: {Block, WebInfo}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../sass/footer.scss';
  .footer{
    position: relative;
    @extend %width;
    height: $footerheight;
    background-color: #fff;
    border-top: 1px solid #e2e2e2;
  }

</style>



